pre {
  max-height: 100px;
  overflow: auto; }

.m-slider__content {
  margin: 0;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  width: 100%; }

.m-slider__wrap {
  position: relative;
  display: inline-block;
  width: 100%; }

.m-slider__arrow {
  position: absolute;
  top: 0;
  background: red;
  z-index: 10; }
  .m-slider__arrow--next {
    right: 0; }
  .m-slider__arrow--prev {
    left: 0%; }

.m-slider__item {
  font-size: 96px;
  color: #ccc;
  text-align: center;
  width: 100%;
  height: 200px;
  display: inline-block;
  box-sizing: border-box;
  list-style: none;
  vertical-align: top; }

.m-slider__dot {
  list-style: none;
  display: inline-block;
  vertical-align: middle;
  margin: 0 5px; }
  .m-slider__dot span {
    width: 20px;
    height: 20px;
    background: #ccc;
    border-radius: 50%;
    text-indent: -9999%;
    display: inline-block;
    cursor: pointer; }
  .m-slider__dot--active span {
    background: #999; }
